/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

#icon-sprite {
  display: none;
}

.inner-body {
  position: relative;
  width: 100%;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a {
  color: #256bd1;
}

a:hover, a:focus {
  color: #3585f9;
}

a:active {
  color: #256bd1;
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* -----------------------------------------------------------------------------
  Defaults for content (inc)

  General complete style specifically required for the content, including
  lists, tables, etc. that is not necessarily need in all pages.
----------------------------------------------------------------------------- */
/* Fixes the anchor */
.section {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.section::before {
  content: '';
  display: block;
  position: relative;
  width: 0;
  height: 1rem;
  margin-top: -1rem;
}

